<template>
  <v-container fluid>
    <v-row>
      <v-col cols="12" md="6">
        <v-text-field
          label="Description"
          type="text"
          autocomplete="off"
          dense
          v-model="record.name"
        ></v-text-field>
      </v-col>

      <v-col cols="12" md="6">
        <v-text-field label="Alt Tag" type="text" autocomplete="off" dense v-model="record.alt_tag"></v-text-field>
      </v-col>

      <v-col cols="3">
        <v-autocomplete
          autocomplete="off"
          dense
          v-model="record.category_url"
          :items="categories"
          label="Category"
          item-text="name"
          item-value="url"
          @change="autofillRecords(record.category_url)"
        ></v-autocomplete>
      </v-col>

      <v-col cols="3">
        <v-autocomplete
          autocomplete="off"
          dense
          v-model="record.subcategory_url"
          :items="subcategories"
          label="Sub Category"
          item-text="name"
          item-value="url"
        ></v-autocomplete>
      </v-col>

      <v-col cols="3">
        <v-autocomplete
          autocomplete="off"
          dense
          v-model="record.brand_url"
          :items="brands"
          label="Brand"
          item-text="name"
          item-value="url"
        ></v-autocomplete>
      </v-col>

      <v-col cols="3">
        <v-autocomplete
          autocomplete="off"
          dense
          v-model="record.country"
          :items="countries"
          label="Country Of Origin"
          item-text="name"
          item-value="url"
        ></v-autocomplete>
      </v-col>

      <v-col cols="12" md="3">
        <v-text-field
          label="Alcohol Percentage"
          type="text"
          autocomplete="off"
          dense
          v-model="record.alcohol"
        ></v-text-field>
      </v-col>

      <v-col cols="12" md="12">
        <v-text-field
          label="Meta Title"
          type="text"
          autocomplete="off"
          dense
          v-model="record.meta_title"
        ></v-text-field>
      </v-col>

      <v-col cols="12" md="12">
        <v-textarea
          label="Meta Description"
          type="text"
          autocomplete="off"
          dense
          v-model="record.meta_desc"
        ></v-textarea>
      </v-col>

      <v-col cols="12">
        <html-editor ref="footer_content"></html-editor>
      </v-col>
    </v-row>

    <!-- create images section -->
    <v-row style="margin-top: 50px">
      <v-col cols="12">
        <label>Cover Image</label>
        <file-upload @image="setCoverImage"></file-upload>
      </v-col>

      <v-col cols="6">
        <img
          v-if="coverImageUrl"
          :src="coverImageUrl"
          style="width: 350px; height: 350px; padding: 20px"
        />
      </v-col>
    </v-row>

    <v-row v-show="pageStatus == 'create'">
      <v-col cols="12">
        <label>Other Images</label>
        <multi-upload @collection="setImages" @urls="setImagesUrls"></multi-upload>
      </v-col>

      <v-col cols="3" v-for="(url, key) in imagesUrls" v-bind:key="key">
        <img v-if="url" :src="url" style="width: 350px; height: 250 padding: 20px" />
        <v-btn icon @click="removeOtherImage(key)">
          <v-icon color="red">mdi-close</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <!-- end of create images section -->

    <!-- update images section -->
    <v-row v-show="pageStatus == 'view'" style="margin-top: 50px">
      <v-col cols="4">
        <v-img :src="`${serverUrl}/uploads/products/${record.coverImage}`"></v-img>
      </v-col>
    </v-row>

    <v-row v-show="pageStatus == 'view'">
      <v-col cols="4" v-for="(image, index) of record.otherImages" v-bind:key="index">
        <v-img :src="`${serverUrl}/uploads/products/${image}`"></v-img>
      </v-col>
    </v-row>
    <!-- end of update images section -->

    <v-row>
      <v-col cols="12">
        <v-btn color="accent" v-show="action == 'create'" @click="sendData" :loading="loader">
          <v-icon left>mdi-content-save</v-icon>Save
        </v-btn>

        <v-btn color="accent" v-show="action == 'view'" @click="sendData" :loading="loader">
          <v-icon left>mdi-content-save</v-icon>Update
        </v-btn>
      </v-col>
    </v-row>
    <snackbar ref="snackbar"></snackbar>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import FileUpload from "@/components/global/file-uploader.vue";
import HtmlEditor from "@/components/global/html-editor.vue";
import MultiUpload from "@/components/global/multiple-file-uploader.vue";
export default {
  computed: {
    ...mapGetters(["serverUrl"]),
  },
  components: {
    FileUpload,
    MultiUpload,
    HtmlEditor,
  },
  props: {
    action: {
      type: String,
    },
    initial: {
      type: Object,
    },
  },
  data() {
    return {
      sizes: [],
      record: {},
      pageStatus: "",
      loader: false,
      coverImage: null,
      images: [],
      imagesUrls: [],
      coverImageUrl: null,
      categories: [],
      countries: [],
      subcategories: [],
      brands: [],
    };
  },
  watch: {
    action: {
      handler: "setAction",
      immediate: true,
    },
    initial: {
      handler: "setInitial",
      immediate: true,
    },
  },
  methods: {
    initializeContent() {
      setTimeout(() => {
        if (
          this.record.content &&
          this.record.content !== "" &&
          this.$refs.footer_content
        ) {
          this.$refs.footer_content.setContent(this.record.content);
        }
      }, 100);
    },
    setAction(val) {
      this.pageStatus = val;
    },
    setInitial(val) {
      this.record = { ...val };
      this.initializeContent();
      this.autofillRecords(val.category_url);
    },
    sendData() {
      const productContent = this.$refs.footer_content.getContent();

      const formData = new FormData();
      const url = this.record.name
        .toLowerCase()
        .replace(/[^\w\s]/gi, "")
        .trim()
        .split(" ")
        .join("-");
      this.record.url = url;

      formData.append("coverImage", this.coverImage);
      formData.append("giftpack", true);
      formData.append("content", productContent);
      formData.append("introText", this.introText);
      formData.append("product", JSON.stringify(this.record));
      //other images
      const files = this.images;
      files.forEach((file) => {
        const m_file = file;
        formData.append("otherImages", m_file);
      });
      this.loader = true;
      this.$emit("data", formData);
    },
    setCoverImage({ image, imageUrl }) {
      this.coverImage = image;
      this.coverImageUrl = imageUrl;
    },
    setImages(files) {
      this.images = files;
    },
    setImagesUrls(urls) {
      this.imagesUrls = urls;
    },
    removeOtherImage(key) {
      this.imagesUrls.splice(key, 1);
      this.images.splice(key, 1);
    },
    getSizes() {
      const self = this;
      this.$store
        .dispatch("get", `/sizes`)
        .then((res) => {
          if (res.state) {
            self.sizes = res.records;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch((err) => {
          this.$refs.snackbar.show(err, "red");
        });
    },
    getCategories() {
      const self = this;
      this.$store
        .dispatch("get", `/categories`)
        .then((res) => {
          if (res.state) {
            self.categories = res.records;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch((err) => {
          this.$refs.snackbar.show(err, "red");
        });
    },
    autofillRecords(url) {
      const self = this;
      this.$store
        .dispatch("get", `/categories/autofill/data/${url}`)
        .then((res) => {
          if (res.state) {
            self.subcategories = res.subcategories;
            self.brands = res.brands;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch((err) => {
          this.$refs.snackbar.show(err, "red");
        });
    },
    getCountries() {
      const self = this;
      this.$store
        .dispatch("get", `/countries`)
        .then((res) => {
          if (res.state) {
            self.countries = res.records;
          } else {
            this.$refs.snackbar.show(res.msg, "red");
          }
        })
        .catch((err) => {
          this.$refs.snackbar.show(err, "red");
        });
    },
  },
  created() {
    Promise.all([this.getCategories(), this.getSizes(), this.getCountries()]);
  },
};
</script>

